import { Component, OnInit } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { MatIconRegistry } from '@angular/material/icon';
import { PostsService } from "../posts/posts.service";
import { Router, NavigationExtras } from '@angular/router';
import { HardcodedAuthencationService } from '../service/hardcoded-authencation.service';


@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  username = 'Patrick';
  password = '';
  invalidLogin = false;
  userIsLogin = false;

  private user = {
    name: '',
    password: '',
    email: '',
    phone: ''
  };

  public logIn = {
    isInvalidLogin: false,
    isCreateNewAccount: false,
    userCreated: false,
    isDisable: false
  };

  isDisable: boolean;

  constructor(public postsService: PostsService, private router: Router,
    private hardcodedAuthticationService: HardcodedAuthencationService) { }

  ngOnInit() {

  }

  checkLogin() {
    this.postsService.getUser().subscribe(data => this.isValidUser(data));
  }

  handleLogin() {
    if (this.hardcodedAuthticationService.authenticate(this.username, this.password)) {
      this.router.navigate(['todos']);
      this.invalidLogin = false;
    } else {
      this.invalidLogin = true;
    }

  }

  isValidUser(arrObject) {
    for (let i = 0; i < arrObject.length; i++) {
      const element = arrObject[i];
      if (element.name === this.user.name && element.password === this.user.password) {
        this.router.navigate(['welcome']);
        this.logIn.isInvalidLogin = false;
        this.postsService.setUser(element.name, element.password);
      } else {
        this.logIn.isInvalidLogin = true;
      }
    }
  }

  // this is for bash login
  checkLoginBash() {
    this.logIn.isDisable = true;
    this.postsService.getUser().subscribe(data => this.isValidUserBash(data));
  }

  isValidUserBash(arrObject) {
    this.logIn.isInvalidLogin = false;
    for (let i = 0; i < arrObject.length; i++) {
      const element = arrObject[i];

      if(this.hardcodedAuthticationService.BashAuthenticate(this.user.name, this.user.password, element.name, element.password)) {
        this.userIsLogin = true;

        setTimeout(() => {
          this.postsService.setLoginUserInfo(element);
          this.logIn.isDisable = false;
          this.router.navigate(['shop']);
        }, 50);
        return element;
      } else {
        this.logIn.isInvalidLogin = true;
        this.logIn.isDisable = false;
      };
    }
  }

  setCreateAccount() {
    this.logIn.isCreateNewAccount = true;
  }

  createNewUser() {
    this.logIn.isDisable = true;

    this.postsService.createNewUser(this.user).subscribe(data => {
      setTimeout(() => {
        this.logIn.isDisable = false;
      }, 50);
    });
    this.logIn.isCreateNewAccount = false;
    this.logIn.userCreated = true;
  }
}
