import { Component, OnInit, OnDestroy, Input } from "@angular/core";
import { Subscription } from 'rxjs';
import { Post } from "../post.model";
import { PostsService } from "../posts.service";
import {NgForm} from '@angular/forms';
import { Zillow } from "../post.model";
import { Route } from "@angular/compiler/src/core";
import { Router, ActivatedRoute } from "@angular/router";

@Component({
  selector: "app-post-list",
  templateUrl: "./post-list.component.html",
  styleUrls: ["./post-list.component.css"]
})
export class PostListComponent implements OnInit, OnDestroy {
  private posts: Post[] = []
  private postSpring: Post[] = []
  private postsSub: Subscription
  public show: boolean = true
  private isSearchHome = false
  private homeInfo: any[]
  private arrHome: Zillow[] = []
  private search: string = ""
  private homeInfoSub: Subscription
  private user = { }
  public user2: string
  @Input() homeInfoZillow: string
  @Input() bankName: string
  @Input() zillowObj: Zillow
  @Input() street: string


  constructor( 
    public postsService: PostsService,
    private router: Router
    ) {  }

  ngOnInit() {
    this.user = this.postsService.getUserSpring();
       
    this.postsService.getUser().subscribe(data => null);

    this.user2 = sessionStorage.getItem('authenticaterUser');

    this.getNewPostZpid()
  }

  ngOnDestroy() {

  }

  //refresh all posts
  getNewPost() {
    return this.postsService.getPostSpring().subscribe(data => this.getSpringPost(data));
  }

  getNewPostZpid() {
    return this.postsService.getPostSpringByZpid(this.postsService.getZillowData()._id).subscribe(data => this.getSpringPost(data));
  }

  //new search buttom
  newSearch() {
    this.postsService.setShowSearchResult(true, false)
  }

  //submit buttom get new posts
  getSpringPost(data) {
    this.postSpring = data
  }

  //submit button 
  onAddPostSpring(form: NgForm) {
    let currentDate = new Date();

    if (form.invalid) {
      return;
    }

    //fixing edit return to submit error
    let postsObj = {
      user_id: 1000003,
      name: sessionStorage.getItem('authenticaterUser'), 
      description: form.value.description,
      date: currentDate,
      street: this.zillowObj.street,
      city: this.zillowObj.city,
      state: this.zillowObj.state,
      zip_code: this.zillowObj.zipcode,
      amount: this.zillowObj.price,
      low: this.zillowObj.lowPrice,
      high: this.zillowObj.highPrice,
      zpid: this.postsService.getZillowData()._id,
      home_details: this.zillowObj.homeDetail,
      last_updated: this.zillowObj.lastUpdate
    }
    
    this.postsService.addPostSpring(postsObj).subscribe(data => 
      {
        //refresh by zpid posts only
        this.getNewPostZpid()
      });
    
    form.resetForm();
  }

  onDeleteSpringPost(id) {
    this.postsService.deletePostSpring(id).subscribe(data => this.getNewPostZpid());
  }

  //Edit click button
  onUpdateSpringPost(id) {
    this.router.navigate(['todoEdit', id])
  }

  onDelete(postId: string) {
    this.postsService.deletePost(postId);
  }

  getZillow() {
    this.postsService.getZillow().subscribe(data => console.log(data.error.text));
  }

  onGetZillow3(form: NgForm) {
    this.postsService.getZillow3(form.value.address);
    this.arrHome = this.postsService.getZillowSearchArr();
  }

  onGetGiphy() {
    return this.postsService.getGiphy();
  }
}










