import { Component, OnInit } from '@angular/core';
import { HardcodedAuthencationService } from '../service/hardcoded-authencation.service'

@Component({
  selector: 'app-logout',
  templateUrl: './logout.component.html',
  styleUrls: ['./logout.component.css']
})
export class LogoutComponent implements OnInit {

  constructor(
    private hardcodedAuthencationService: HardcodedAuthencationService
  ) { }

  ngOnInit() {
    this.hardcodedAuthencationService.logout();
  }

}
