import { Component, OnInit, Input } from '@angular/core';
import { PostsService } from "../posts/posts.service";
import { HttpClient } from "@angular/common/http";
import { Zillow } from '../posts/post.model';

@Component({
  selector: 'app-real-estate',
  templateUrl: './real-estate.component.html',
  styleUrls: ['./real-estate.component.css']
})

export class RealEstateComponent implements OnInit {
  private homeInfo: any[]
  private search: string = "2719 Colonial Dr, Carrollton, TX"
  public homeInfo3
  @Input() bankName: string
  private isSearching: boolean
  private showResult: boolean
  public searchResult = 
    { 
      isInvalidSearch: false,
      isInputAddressInvalid: false 
    }
  private error: string

  constructor(public postsService: PostsService, private http: HttpClient) {
    this.homeInfo3 = {};
    this.isSearching = true
    this.showResult = false
  }

  ngOnInit() {}

  printHomeInfo() {
    console.log(this.bankName);
  }

  getZillow3(address: string) {
    this.http.get<any>('http://localhost:3000/api/zillow/' + address).subscribe((response) => {
      const parser = new DOMParser();
      let xml = parser.parseFromString(response.Posts, 'text/xml');

      let street = xml.getElementsByTagName("street")[0].childNodes[0];
      let zip = xml.getElementsByTagName("zipcode")[0].childNodes[0];
      let city = xml.getElementsByTagName("city")[0].childNodes[0];
      let state = xml.getElementsByTagName("state")[0].childNodes[0];
      let amount = xml.getElementsByTagName("amount")[0].childNodes[0];
      let homeDetails = xml.getElementsByTagName("homedetails")[0].childNodes[0];

      this.homeInfo = [];
      this.homeInfo.push(new XMLSerializer().serializeToString(street));
      this.homeInfo.push(new XMLSerializer().serializeToString(zip));
      this.homeInfo.push(new XMLSerializer().serializeToString(city));
      this.homeInfo.push(new XMLSerializer().serializeToString(state));
      this.homeInfo.push(new XMLSerializer().serializeToString(amount));
      this.homeInfo.push(new XMLSerializer().serializeToString(homeDetails));

      return this.homeInfo;
    })
  }

  getHomeInfo() {
    return this.homeInfo;
  }

  //example post to database
  onAddPostSpring(zillowData) {
    let currentDate = new Date();

    let postsObj = {
      street: zillowData.street,
      city: zillowData.city,
      date: currentDate,
      zpid: zillowData._id,
      state: zillowData.state
    }

    this.postsService.addPostSpring(postsObj).subscribe(data => {
      // console.log(data)
    });
  }

  onGetZillow() {
    this.postsService.getZillow().subscribe(data => {
      this.processXml(data)
    },
    err => console.error(err))
  }


  //testing processXml for error
  processXml2(data) {
    const parser = new DOMParser();
    let xml = parser.parseFromString(data.message, 'text/xml');

    let errorTag = xml.getElementsByTagName("code")[0].childNodes[0];
    this.error = new XMLSerializer().serializeToString(errorTag)

    if (this.error === '0') {
      let street = xml.getElementsByTagName("street")[0].childNodes[0];
      let zip = xml.getElementsByTagName("zipcode")[0].childNodes[0];
      let city = xml.getElementsByTagName("city")[0].childNodes[0];
      let state = xml.getElementsByTagName("state")[0].childNodes[0];
      let amount = xml.getElementsByTagName("amount")[0].childNodes[0];
      let homeDetails = xml.getElementsByTagName("homedetails")[0].childNodes[0];
      let lowPrice = xml.getElementsByTagName("low")[0].childNodes[0];
      let highPrice = xml.getElementsByTagName("high")[0].childNodes[0];
      let id = xml.getElementsByTagName("zpid")[0].childNodes[0];
      let lastUpdate = xml.getElementsByTagName("last-updated")[0].childNodes[0];

      this.homeInfo3 = {
        street: new XMLSerializer().serializeToString(street),
        city: new XMLSerializer().serializeToString(city),
        state: new XMLSerializer().serializeToString(state),
        zipcode: new XMLSerializer().serializeToString(zip),
        price: new XMLSerializer().serializeToString(amount),
        homeDetail: new XMLSerializer().serializeToString(homeDetails),
        lowPrice: new XMLSerializer().serializeToString(lowPrice),
        highPrice: new XMLSerializer().serializeToString(highPrice),
        _id: new XMLSerializer().serializeToString(id),
        lastUpdate: new XMLSerializer().serializeToString(lastUpdate)
      };

      this.postsService.setZillowData(this.homeInfo3);
      this.postsService.setShowSearchResult(false, true)
    } else {
      this.searchResult.isInvalidSearch = true
    }
  }

  processXml(data) {
    const parser = new DOMParser();
    let xml = parser.parseFromString(data.message, 'text/xml');

    let errorTag = xml.getElementsByTagName("code")[0].childNodes[0];
    this.error = new XMLSerializer().serializeToString(errorTag)

    let street = xml.getElementsByTagName("street")[0].childNodes[0];
    let zip = xml.getElementsByTagName("zipcode")[0].childNodes[0];
    let city = xml.getElementsByTagName("city")[0].childNodes[0];
    let state = xml.getElementsByTagName("state")[0].childNodes[0];
    let amount = xml.getElementsByTagName("amount")[0].childNodes[0];
    let homeDetails = xml.getElementsByTagName("homedetails")[0].childNodes[0];
    let lowPrice = xml.getElementsByTagName("low")[0].childNodes[0];
    let highPrice = xml.getElementsByTagName("high")[0].childNodes[0];
    let id = xml.getElementsByTagName("zpid")[0].childNodes[0];
    let lastUpdate = xml.getElementsByTagName("last-updated")[0].childNodes[0];

    this.homeInfo3 = {
      street: new XMLSerializer().serializeToString(street),
      city: new XMLSerializer().serializeToString(city),
      state: new XMLSerializer().serializeToString(state),
      zipcode: new XMLSerializer().serializeToString(zip),
      price: new XMLSerializer().serializeToString(amount),
      homeDetail: new XMLSerializer().serializeToString(homeDetails),
      lowPrice: new XMLSerializer().serializeToString(lowPrice),
      highPrice: new XMLSerializer().serializeToString(highPrice),
      _id: new XMLSerializer().serializeToString(id),
      lastUpdate: new XMLSerializer().serializeToString(lastUpdate)
    };

    this.postsService.setZillowData(this.homeInfo3);
  }

  //testing zillow full address
  onGetZillowAddress() {
    let address = ["833+Canyon+Crest+Dr", "Irving", "TX"]
    let addressSplit = this.search.split(",")

    //check for valid address search
    if(addressSplit[0] === undefined || addressSplit[1] === undefined || addressSplit[2] === undefined) {
      this.searchResult.isInputAddressInvalid = true
    } else {
      address = [addressSplit[0].trim(), addressSplit[1].trim(), addressSplit[2].trim()]  
      
      this.postsService.getZillowFullAddress(address).subscribe(data => {
        this.processXml2(data)
      })
    }
  }
}