import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { PostsService } from '../posts/posts.service';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { DomSanitizer } from '@angular/platform-browser';
import { HardcodedAuthencationService } from "../service/hardcoded-authencation.service";
import { Router, NavigationExtras } from '@angular/router';

declare var paypal;

export interface ImageModel {
  id: string;
  name: string;
  type: string;
  picByte: any;
  retrievedImage: any;
}

export interface ImgurData {
  data: any[];
  status: string;
  success: string;
}


@Component({
  selector: 'app-shop',
  templateUrl: './shop.component.html',
  styleUrls: ['./shop.component.css']
})
export class ShopComponent implements OnInit {
  @ViewChild('paypal', {static: true}) paypalElement: ElementRef;

  private items = [{
    id: 1,
    name: 'Jasmine Milk Tea',
    description: 'Fresh made on order',
    price: '5.45',
    quantity: 2,
    sellerName: 'I-Tea',
    email: 'ITeas@gmail.com',
    phone: '510-451-3391'
  }];

  product = {
    id: 1,
    name: 'Custom Gaming/Productivity PC',
    description: 'Brand new',
    img: 'pc.img',
    price: 1000,
    currency: 'USD',
    quantity: 1,
    total: 1000,
    seller: 'Patrick Luu',
    email: '',
    phone: ''
  };

  paidFor = false;
  //testing upload pic
  selectedFile: File;
  retrievedImage: any;
  retrieveResonse: any;
  base64Data: any;
  thumbnail: any;

  //testing image arrray
  imageArray: any[];
  imageArrayModel: ImageModel[];
  currentImage: ImageModel;
  user2: string;
  imgurPictures: any;
  imgurArray = [];
  testStyle: boolean = true;



  constructor(public postsService: PostsService, private http: HttpClient, 
    private sanitizer: DomSanitizer, public hardcodedAuthencationService: HardcodedAuthencationService,
    public router: Router) { }

  ngOnInit() {
    //refresh all item from springboot
    this.getItemList2();

    this.postsService.getImages().subscribe(res => {
      this.imgurPictures = res;
      for (let i = 0; i < this.imgurPictures.data.length; i++) {
        this.imgurArray.push({'link': this.imgurPictures.data[i].link, 'id': this.imgurPictures.data[i].id});
      }
    }, error => {
      console.log(error);
    });

    this.user2 = sessionStorage.getItem('authenticaterUser');

  }

  getItemList2() {
    this.postsService.getItemShop();
  }

  //get user already login info
  getUserLoginInfo() {
    return this.postsService.getLoginUserInfo();
  }

  addToCart2(item) {
    if(this.hardcodedAuthencationService.isUserLoggedIn() === false) {
      this.router.navigate(['login']);
    }
    this.postsService.addToCart(item);
  }

  getProductListing() {
    // console.log(this.postsService.getProduct());
    return this.postsService.getProduct();
  }

  // tslint:disable-next-line: comment-format
  //Paypal submit button
  submitPayment() {
    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json',
        'Access-Control-Allow-Origin': '*',
        'Access-Control-Allow-Credentials': 'true',
        'Access-Control-Allow-Headers': 'Origin, Content-Type, X-Auth-Token',
        'Access-Control-Allow-Methods': 'GET, POST, PATCH, PUT, DELETE, OPTIONS'
      })
    };

    const cartItem = this.postsService.getCart();
    const result = {
      name: '',
      description: '',
      currency: '',
      quantity: 0,
      seller: '',
      img: '',
      total: 0,
      price: 0,
      id: 1,
      email: '',
      phone: ''
    };

    cartItem.forEach(function(item, idx) {
      result.name += item.name;
      result.description = item.description;
      result.currency = item.currency;
      result.quantity = item.quantity;
      result.seller = item.seller;
      result.img = item.img;
      result.id = item.id;

    });

    result.email = this.postsService.getLoginUserInfo().email;
    result.phone = this.postsService.getLoginUserInfo().phone;

    result.total = this.postsService.getTotal();
    result.price = this.postsService.getTotal();

    this.product = result;

    return this.http.post('http://localhost:3000/pay', this.product, httpOptions).subscribe(data => this.processPayment(data));
  }

  processPayment(data) {
    // console.log(data.url)
    let url = data.url;
    window.location.href = url;
  }

  submitPayment2() {
    return this.http.get('http://localhost:3000/api/giphy').subscribe(data => {
      // this.result = data
      // console.log(data);
    });
  }

  getImage() {
    this.postsService.getImageService()
      .subscribe(
        res => {
          this.retrieveResonse = res;
          this.base64Data = this.retrieveResonse.picByte;
          this.retrievedImage = 'data:image/jpeg;base64,' + this.base64Data;
        }
      );
  }

  getImage2(imageName) {
    this.postsService.getImageService2(imageName)
      .subscribe(
        res => {
          this.retrieveResonse = res;
          this.base64Data = this.retrieveResonse.picByte;
          this.retrievedImage = 'data:image/jpeg;base64,' + this.base64Data;
        }
      );
  }

  getImageArray() {
    this.postsService.getImageServiceArray()
      .subscribe(
        res => {
          this.retrieveResonse = res;
          this.base64Data = this.retrieveResonse[0].picByte;
          this.retrievedImage = 'data:image/jpeg;base64,' + this.base64Data;
          let objectURL = 'data:image/jpeg;base64,' + this.base64Data;

          this.thumbnail = this.sanitizer.bypassSecurityTrustUrl(objectURL);
        }
      );
  }

}
