import { Component, OnInit, Input } from '@angular/core';
import { WelcomeDataService } from '../service/data/welcome-data.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-wellcome',
  templateUrl: './wellcome.component.html',
  styleUrls: ['./wellcome.component.css']
})
export class WellcomeComponent implements OnInit {
  [x: string]: any;

  welcomeMessageFromService: string
  name: string = "Patrick"
  @Input('welcome') welcome: string;
  user2: string


  constructor(
    private service:WelcomeDataService,
    private router: Router) { }


  ngOnInit() {
    this.user2 = sessionStorage.getItem('authenticaterUser');

  }

  clickNext() {
    this.router.navigate(['realEstate']);
  }

  getWelcomeMessage() {
    this.service.executeHelloWorldBeanService().subscribe(
      data => this.handleSuccessfulResponseBean(data),
      error => this.handleErrorResponse(error)
      );
  }

  getWelcomeMessageWithParameter() {
    this.service.executeHelloWorldBeanServiceVariable(this.name).subscribe(
      data => this.handleSuccessfulResponse(data),
      error => this.handleErrorResponse(error)
      );
  }

  handleErrorResponse(error) {
    console.log(error);
  }

  handleSuccessfulResponse(response) {
    this.welcomeMessageFromService = response.message;
  }

  handleSuccessfulResponseBean(response) {
    this.welcomeMessageFromService = response[0].name;
  }

}
