import { BrowserModule } from "@angular/platform-browser";
import { BrowserAnimationsModule } from "@angular/platform-browser/animations";
import { NgModule } from "@angular/core";
import { FormsModule } from "@angular/forms";
import { HttpClientModule } from "@angular/common/http";
import {
  MatInputModule,
  MatCardModule,
  MatButtonModule,
  MatToolbarModule,
  MatExpansionModule
} from "@angular/material";
import { AppComponent } from "./app.component";
import { PostCreateComponent } from "./posts/post-create/post-create.component";
import { HeaderComponent } from "./header/header.component";
import { PostListComponent } from "./posts/post-list/post-list.component";
import { LoginComponent } from './login/login.component';
import { RouterModule, Routes } from '@angular/router';
import { RealEstateComponent } from './real-estate/real-estate.component';
import { MatIconModule } from "@angular/material/icon";
import { TodosComponent } from './todos/todos.component';
import { ErrorComponent } from './error/error.component';
import { LogoutComponent } from './logout/logout.component';
import { RouteGuardService } from "./service/route-guard.service";
import { WellcomeComponent } from './wellcome/wellcome.component';
import { UpdateBashComponent } from './update-bash/update-bash.component';
import { TodoEditComponent } from './todo-edit/todo-edit.component';
import { ShopComponent } from './shop/shop.component';
import { GoogelPaymentComponent } from './googel-payment/googel-payment.component';
import { SuccessPaymentComponent } from './success-payment/success-payment.component';
import { ListItemComponent } from './list-item/list-item.component';
import { RemoveItemComponent } from './remove-item/remove-item.component';
import { CartComponent } from './cart/cart.component';
import { ManualPaypalComponent } from './manual-paypal/manual-paypal.component';


const appRoutes: Routes = [
  { path: '', redirectTo: '/shop', pathMatch: 'full' },
  { path: 'login', component: LoginComponent },
  { path: 'create', component: PostCreateComponent },
  { path: 'shop', component: ShopComponent },
  // { path: 'dialog', component: DialogComponent },
  { path: 'manualPaypal', component: ManualPaypalComponent, canActivate:[RouteGuardService] },
  { path: 'cart', component: CartComponent, canActivate:[RouteGuardService] },
  { path: 'remove', component: RemoveItemComponent, canActivate:[RouteGuardService] },
  { path: 'listItem', component: ListItemComponent, canActivate:[RouteGuardService] },
  { path: 'success', component: SuccessPaymentComponent, canActivate:[RouteGuardService] },
  { path: 'realEstate', component: RealEstateComponent, canActivate:[RouteGuardService] },
  { path: 'list', component: PostListComponent, canActivate:[RouteGuardService] },
  { path: 'todos', component: TodosComponent, canActivate:[RouteGuardService] },
  { path: 'logout', component: LogoutComponent, canActivate:[RouteGuardService] },
  { path: 'welcome', component: WellcomeComponent, canActivate:[RouteGuardService] },
  { path: 'todoEdit/:id', component: TodoEditComponent, canActivate:[RouteGuardService] },
  // { path: '**', component: ErrorComponent }
];


@NgModule({
  declarations: [
    AppComponent,
    PostCreateComponent,
    HeaderComponent,
    PostListComponent,
    LoginComponent,
    RealEstateComponent,
    TodosComponent,
    ErrorComponent,
    LogoutComponent,
    WellcomeComponent,
    UpdateBashComponent,
    TodoEditComponent,
    ShopComponent,
    GoogelPaymentComponent,
    SuccessPaymentComponent,
    ListItemComponent,
    // DialogComponent,
    RemoveItemComponent,
    CartComponent,
    ManualPaypalComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    BrowserAnimationsModule,
    MatInputModule,
    MatIconModule,
    MatCardModule,
    MatButtonModule,
    MatToolbarModule,
    MatExpansionModule,
    HttpClientModule,
    RouterModule.forRoot(
      appRoutes,
      { enableTracing: false } // <-- debugging purposes only
    )
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule {}
