import { Component, OnInit } from '@angular/core';
import { PostsService } from '../posts/posts.service';
import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Router } from '@angular/router';

@Component({
  selector: 'app-list-item',
  templateUrl: './list-item.component.html',
  styleUrls: ['./list-item.component.css']
})
export class ListItemComponent implements OnInit {

  public item = {
    name: '',
    description: 'new',
    img: Blob,
    picByte: File,
    price: '20',
    currency: 'USD',
    quantity: 1,
    total: '20',
    seller: 'Patrick Luu',
    phone: '510-115-4515',
    email: 'user@gmail.com',
    imageName: '',
    imageUrl: '',
    imageId: '',
    imageDeletehash: ''
  };

  //testing upload pic
  selectedFile: File;
  retrievedImage: any;
  base64Data: any;
  retrieveResonse: any;
  message: string;
  imageName: any;
  counter: number;
  imgurArray: any;
  isDisable = false;


  constructor(public postsService: PostsService, private httpClient: HttpClient, private router: Router) { }

  ngOnInit() {
  }

  //add item on submit button
  addItem() {
    this.isDisable = true;
    this.item.total = this.item.price;
    this.item.imageName = this.postsService.getImageName();
    this.postsService.addNewItem(this.item).subscribe(data => {
        setTimeout(() => {
          this.isDisable = false;
          this.router.navigate(['shop']);
        }, 500);
      });
  }

  addPic() {
    this.item.total = this.item.price;
    this.postsService.addNewItem2(this.item).subscribe(data => console.log(data));
  }

  //testing upload pic
  //Gets called when the user selects an image
  public onFileChanged(event) {
    this.selectedFile = event.target.files[0];
  }

  //Gets called when the user clicks on submit to upload the image --old
  onUpload2() {
    const uploadImageData = new FormData();
    uploadImageData.append('imageFile', this.selectedFile, this.selectedFile.name);

    this.item.imageName = this.selectedFile.name;

    this.httpClient.post('http://localhost:9090/jpa/shop/items2', uploadImageData, { observe: 'response' })
      .subscribe((response) => {
        if (response.status === 200) {
          this.message = 'Image uploaded successfully';
        } else {
          this.message = 'Image not uploaded successfully';
        }
      });
  }

  //Gets called when the user clicks on submit to upload the image --old
  onUpload() {
    let counter = 0;
    ++counter;
    this.imageName = this.selectedFile.name + counter;

    const uploadImageData = new FormData();
    uploadImageData.append('imageFile', this.selectedFile, this.imageName);

    this.item.imageName = this.selectedFile.name;

    this.httpClient.post('http://localhost:9090/jpa/shop/items2', uploadImageData, { observe: 'response' })
      .subscribe((response) => {
        if (response.status === 200) {
          this.message = 'Image uploaded successfully';
        } else {
          this.message = 'Image not uploaded successfully';
        }
      });
  }

  //upload image to Imgur
  onUploadService() {
    this.isDisable = true;

    this.postsService.postImages(this.selectedFile)
    .subscribe((response) => {
      // console.log('imgur upload data');
      // console.log(response);
      this.imgurArray = response;
      // console.log(this.imgurArray.data.link);
      this.item.imageUrl = this.imgurArray.data.link;
      this.item.imageDeletehash = this.imgurArray.data.deletehash;
      this.item.imageId = this.imgurArray.data.id;
      // console.log(this.item);
      setTimeout(() => {
        this.isDisable = false;
      }, 10);
    });
  }

  //get image database
  getImage() {
    this.httpClient.get('http://localhost:9090/jpa/shop/get/' + this.imageName)
      .subscribe(
        res => {
          // console.log(res);
          this.retrieveResonse = res;
          this.base64Data = this.retrieveResonse.picByte;
          this.retrievedImage = 'data:image/jpeg;base64,' + this.base64Data;
        }
      );
  }

  getImage2() {
    this.postsService.getImageService()
      .subscribe(
        res => {
          this.retrieveResonse = res;
          // console.log(this.retrieveResonse);
          this.base64Data = this.retrieveResonse.picByte;
          this.retrievedImage = 'data:image/jpeg;base64,' + this.base64Data;
        }
      );
  }

  disableButton() {
    console.log("disable clicked")
    this.isDisable = true;

    setTimeout(() => {
      this.isDisable = false;
    }, 3000);
  }

  setDisabled() {
    return this.isDisable = false;
  }

}
