  getZillow3(address: string) {
    this.http.get<any>('https://patrickluubash.com/zillow-bean' + address)
      .subscribe((response) => {
        const parser = new DOMParser();
        let xml = parser.parseFromString(response.Posts, 'text/xml');

        let street = xml.getElementsByTagName("street")[0].childNodes[0];
        let zip = xml.getElementsByTagName("zipcode")[0].childNodes[0];
        let city = xml.getElementsByTagName("city")[0].childNodes[0];
        let state = xml.getElementsByTagName("state")[0].childNodes[0];
        let amount = xml.getElementsByTagName("amount")[0].childNodes[0];
        let homeDetails = xml.getElementsByTagName("homedetails")[0].childNodes[0];
        let lowPrice = xml.getElementsByTagName("low")[0].childNodes[0];
        let highPrice = xml.getElementsByTagName("high")[0].childNodes[0];
        let id = xml.getElementsByTagName("zpid")[0].childNodes[0];
        let lastUpdate = xml.getElementsByTagName("last-updated")[0].childNodes[0];

        let homeInfo3: Zillow = {
          street: new XMLSerializer().serializeToString(street),
          city: new XMLSerializer().serializeToString(city),
          state: new XMLSerializer().serializeToString(state),
          zipcode: new XMLSerializer().serializeToString(zip),
          price: new XMLSerializer().serializeToString(amount),
          homeDetail: new XMLSerializer().serializeToString(homeDetails),
          lowPrice: new XMLSerializer().serializeToString(lowPrice),
          highPrice: new XMLSerializer().serializeToString(highPrice),
          _id: new XMLSerializer().serializeToString(id),
          lastUpdate: new XMLSerializer().serializeToString(lastUpdate)
        };

        this.homeInfo = [];
        this.homeInfo.push(homeInfo3);
        this.homeInfoUpdate.next([...this.homeInfo]);
        return response;
      });
  }

