import { Component } from "@angular/core";
import { PostsService } from "../posts/posts.service";
import { HardcodedAuthencationService } from "../service/hardcoded-authencation.service";

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent {
  private user = {};
  private name: string;
  isUserLoggedIn: boolean = false;
  name2: string;

  constructor(public postsService: PostsService,
    public hardcodedAuthenticationService: HardcodedAuthencationService) {

  }

  ngOnInit() {
    this.isUserLoggedIn = this.hardcodedAuthenticationService.isUserLoggedIn();
    this.name2 = sessionStorage.getItem('authenticaterUser');
  }

  getUserLoginInfo() {
    this.name2 = this.postsService.getLoginUserInfo().name;
    if(this.name2 === undefined || this.name2 === null || this.name2 === '') {
      this.name2 = sessionStorage.getItem('authenticaterUser');
    }
  }
}
