import { Component, OnInit } from '@angular/core';
import { Post } from '../posts/post.model';
import { ActivatedRoute, Router } from '@angular/router';
import { PostsService } from '../posts/posts.service';

@Component({
  selector: 'app-todo-edit',
  templateUrl: './todo-edit.component.html',
  styleUrls: ['./todo-edit.component.css']
})
export class TodoEditComponent implements OnInit {
  id: number
  post: Post

  constructor(
    public postsService: PostsService,
    private router: Router,
    private route: ActivatedRoute
  ) {
    this.post =
    {
      id: "",
      title: "",
      content: "",
      description: "",
      name: "",
      date: "02/23/20"
    }

  }

  ngOnInit() {
    //retriving one post to edit
    this.id = this.route.snapshot.params['id'];
    this.postsService.retrievePostSpring(this.id).subscribe(data => this.setPostInstance(data))
  }

  //set api data to local Post instance
  setPostInstance(data) {
    this.post = data
  }

  savePost() {
    this.postsService.updatePostSpring('1', this.post).subscribe(data => {
      this.router.navigate(['realEstate'])
    })
  }

}
